.team {
  padding: 11.5vh 19.27vw;
  align-items: center;

  &__wrap {
    height: 61.6vh;
    display: flex;
  }

  &__container {
    width: 58%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__member {

    &_name {
      font-family: "Poppins", sans-serif;
      font-weight: 800;
      font-size: 38px;
      line-height: 51px;
      letter-spacing: 0.4px;
      color: #00a031;
      text-transform: capitalize;
      margin-bottom: 10px;
    }

    &_position {
      font-family: "Gilroy", sans-serif;
      font-size: 16px;
      line-height: 16px;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: #6c6c6c;
      margin-bottom: 40px;
    }

    &_description {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.2px;
      color: #6c6c6c;
      margin-bottom: 43px;
    }

    &_socials {
      display: flex;
      list-style-position: inside;

      & li {
        margin-right: 35px;
        color: #d8d8d8;
      }

      &_link {
        text-decoration: none;
        font-family: "Gilroy", sans-serif;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 3px;
        text-transform: uppercase;
        color: #6c6c6c;
      }
    }
  }

  &__slider {
    height: 130px;
    background-color: #fbfbfb;
    display: flex;
    justify-content: space-between;
    z-index: 1;

    &_button {
      display: flex;

      &_image {

        width: 91px;
        height: 130px;
        cursor: pointer;
      }

      &_description_next,
      &_description_prev {
        padding-top: 36px;
        padding-left: 20px;
      }

      &_next {
        font-family: "Gilroy", sans-serif;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #00a031;
        margin-right: 10px;
        margin-bottom: 10px;
        display: inline-block;
      }

      &_prev {
        font-family: "Gilroy", sans-serif;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #00a031;
        margin-right: 10px;
        margin-bottom: 10px;
        display: inline-block;
        margin-left: 10px;
      }

      &_arrow {
        color: #00a031;
      }

      &_name_next,
      &_name_prev {
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: 0.2px;
        color: #00a031;
      }
    }
  }

  &__photos {
    position: relative;
    width: 42%;
    height: 100%;

    & > img {
      position: absolute;
      display: none;
    }

    &_first {
      width: 80%;
      height: 100%;
      z-index: 2;
      animation: first_appears 0.5s;
    }

    &_second {
      width: 55.2%;
      height: 90%;
      right: 10%;
      top: 5%;
      opacity: 0.5;
      z-index: 1;
      animation: second_appears 0.5s;
    }

    &_third {
      opacity: 0.2;
      right: 0;
      top: 10%;
      width: 45%;
      height: 80%;
      z-index: 0;
      animation: third_appears 0.5s;
    }

    &_first,
    &_second,
    &_third {
      display: block !important;
    }
  }
}

.activeImageNext {
  opacity: 1 !important;
}

@keyframes info_appears {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes first_appears {
  from {
    width: 55.2%;
    height: 90%;
    right: 10%;
    top: 5%;
    opacity: 0.5;
    z-index: 1;
  }

  to {
    width: 80%;
    height: 100%;
    z-index: 2;
  }
}

@keyframes second_appears {
  from {
    opacity: 0.2;
    right: 0;
    top: 10%;
    width: 45%;
    height: 80%;
    z-index: 0;
  }

  to {
    width: 55.2%;
    height: 90%;
    right: 10%;
    top: 5%;
    opacity: 0.5;
    z-index: 1;
  }
}

@keyframes third_appears {

  to {
    opacity: 0.2;
    right: 0;
    top: 10%;
    width: 45%;
    height: 80%;
    z-index: 0;
  }
}
